import '../styles.css';
import { refs } from '../js/refs';
import {
  countryListMaker,
  countryCardMaker,
} from '../templates/templatesInsert';
import { errorTooMany, errorNotFound } from '../errors/errors';

export function fetchCountries(searchIndex) {
  const fetchUrl = 'https://restcountries.eu/rest/v2/name/';
  refs.container.innerHTML = '';
  return fetch(`${fetchUrl}${searchIndex}`)
    .then(response => {
      if (response.ok) {
        return response.json();
      } else {
        return errorNotFound();
      }
    })
    .then(data => {
      if (data) {
        if (data.length < 2) {
          countryCardMaker(data[0]);
        } else if (data.lenght > 10) {
          errorTooMany();
        } else {
          countryListMaker(data);
        }
      }
    });
}
